<template>
  <div class="member-list">
	  <div class="top">任务分配给指定员工负责，对方在任务开始和快过期会收到 推送.</div>
    <div class="tit">负责人、管理员</div>
    <ul class="list-item">
      <li v-for="(item,index) in adminList" @click="chosenMember(item.id)">
        <span class="name-box" :class="'color'+item.randomcolor">{{nameFormate(item.realName)}}</span>
        <span class="name">{{nameFormate(item.realName)}}</span>
        <i v-show="item.isChosen" class="is-chosen"></i>
      </li>
    </ul>
    <div class="tit">成员</div>
    <ul class="list-item">
      <li v-for="(item,index) in memberList" @click="chosenMember(item.id)">
        <span class="name-box" :class="'color'+item.randomcolor">{{nameFormate(item.realName)}}</span>
        <span class="name">{{nameFormate(item.realName)}}</span>
        <i v-show="item.isChosen" class="is-chosen"></i>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'memberList',
  data () {
    return {
      page:0,
      size:10,
      adminList:[],
      memberList:[],
      chosenList:[{'id':'','name':''}]
    }
  },
  methods:{
    nameFormate(name){
      if(name.length>=2){
        return name.substr(-2,1)+name.substr(-1,1)
      }else{
        return name
      }
    },
    getUrlParam(paraName) {
    　　var url = document.location.toString();
    　　var arrObj = url.split("?");
    　　if (arrObj.length > 1) {
    　　　　var arrPara = arrObj[1].split("&");
    　　　　var arr;
    　　　　for (var i = 0; i < arrPara.length; i++) {
    　　　　　　arr = arrPara[i].split("=");
    					if (arr != null && arr[0] == paraName) {
    　　　　　　　　return arr[1];
    　　　　　　}
    　　　　}
    　　　　return "";
    　　}
    　　else {
    　　　　return "";
    　　}
    },
    chosenMember(userid){
      if(this.getUrlParam("isSelect")=='false'){
        return ;
      }else{
        this.adminList.map((item,index)=>{
          if(item.id==userid){
            item.isChosen=!item.isChosen
            if(item.isChosen){
              this.chosenList.push({
                'id':item.id,
                'realName':item.realName,
              })
            }else{
              this.chosenList.splice(index,1)
            }
          }
        })
        this.memberList.map((item,index)=>{
          if(item.id==userid){
            item.isChosen=!item.isChosen
            if(item.isChosen){
              this.chosenList.push({
                'id':item.id,
                'realName':item.realName,
              })
            }else{
              this.chosenList.splice(index,1)
            }
          }
        })
        console.log(JSON.stringify(this.chosenList));
        window.webkit.messageHandlers.avatarClick.postMessage(JSON.stringify(this.chosenList))
      }
    },
    getMemberlist(that,page){
      var idsArr=[]
      this.chosenList.shift()
      if(that.getUrlParam("ids")){
        idsArr= that.getUrlParam("ids").split(",")
      }
      that.$ajax({
        method: 'GET',
        url: `${that.api}/api/user?page=${page}&size=${that.size}&sort=createTime,desc`
       }).then(function (response) {
  			  response.data.content.map((item,index)=>{
            item.randomcolor=parseInt(Math.random()*12+1,10);
            item.isChosen=false
            if(idsArr.includes((""+item.id))){
              item.isChosen=true
              that.chosenList.push({
                'id':item.id,
                'realName':item.realName,
              })
            }
            if(item.userType ==0){
              that.memberList.push(item)
            }
            if(item.userType ==1){
              that.adminList.push(item)
            }
          })
       }).catch(function (error) {
          console.log(error);
       });
    }
  },
  created(){
    this.getMemberlist(this,this.page)
  }
}
</script>



