import Vue from 'vue'
import Router from 'vue-router'
import taskList from '@/view/taskList'
import memberList from '@/view/memberList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/record',
      name: 'taskList',
      component: taskList,
      meta: {
          title: '操作记录'
      }
    },
    {
      path: '/',
      name: 'taskList',
      component: taskList
    },
    {
      path: '/member',
      name: 'memberList',
      component: memberList,
      meta: {
          title: '成员管理'
      }
    }
  ]
})



