<template>
  <div class="task-list">
    <taskItem :list="recordList"/>
    <div class="loading-wrap" v-show="isLoading">
  		<i></i>
  		<span>加载中...</span>
  	</div>
	  <div class="loaded" v-show="isOver">没有更多数据</div>
  </div>
</template>
<script>
import taskItem from '@/components/taskItem.vue'
export default {
  name: 'taskList',
  data () {
    return {
      page:0,
      size:10,
      recordList:[],
      isLoading: true,
      isOver: false
    }
  },
  components:{
    taskItem
  },
  methods:{
    getTasklist(that,page){
      var url=`${that.api}/api/record?page=${page}&size=${that.size}&sort=createTime,desc`
      if(this.getUrlParam("date")){
        url=`${that.api}/api/record?page=${page}&size=${that.size}&date=${this.getUrlParam("date")}&sort=createTime,desc`
      }
      that.$ajax({
        method: 'GET',
        url:url
       }).then(function (response) {
          if(response.data.content.length < that.size) {
    				that.isOver = true;
    				that.isLoading = false;
    			}
  			  that.recordList = that.recordList.concat(response.data.content)
          console.log(that.recordList);
       }).catch(function (error) {
          console.log(error);
       });
    },
    getUrlParam(paraName) {
    　　var url = document.location.toString();
    　　var arrObj = url.split("?");
    　　if (arrObj.length > 1) {
    　　　　var arrPara = arrObj[1].split("&");
    　　　　var arr;
    　　　　for (var i = 0; i < arrPara.length; i++) {
    　　　　　　arr = arrPara[i].split("=");
    					if (arr != null && arr[0] == paraName) {
    　　　　　　　　return arr[1];
    　　　　　　}
    　　　　}
    　　　　return "";
    　　}
    　　else {
    　　　　return "";
    　　}
    }
  },
  mounted() {
    var that=this;
    window.onscroll = function() {
      var screenHeight = window.screen.height;
      var scrollTop = document.body.scrollTop || document.documentElement.scrollTop;
      var scrollHeight = document.body.scrollHeight || document.documentElement.scrollHeight;
      if (screenHeight + scrollTop >= scrollHeight) {
        if (that.isOver) {
          return;
        }
        that.page++;
        that.getTasklist(that,that.page);
      }
    };
  },
  created(){
    this.getTasklist(this,this.page)
  }
}
</script>



